@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.cdnfonts.com/css/sunday-best?styles=61789');
@import "./assets/fonts/SundayBest.css";
@import './assets/veriables/veriables.module.css';

*{
  box-sizing: border-box;
}

body h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
}
.wrapper{
  max-width: 1140px;
  margin: 0 auto;
}
  
img, svg {
	max-width: 100%;
	height: auto;
}

a, a:link, a:visited  {
  text-decoration: none;
}

a:hover  {
  text-decoration: none;
}
/* Common */
aside, nav, footer, header, section, main {
	display: block;
}

ul, ul li {
	list-style: none;
}
/* Form */
input, textarea, button, select {
	font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
	display: none;
}
button, input[type="submit"] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}


